@import url(https://fonts.googleapis.com/css?family=Titillium+Web:200,300,400,600,700,900|Ultra&display=swap);
body {
  margin: 0px;
  font-family: 'Titillium Web', sans-serif; }

header {
  display: flex;
  justify-content: space-between;
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #FFFFFF;
  opacity: .85; }
  header .social-section {
    margin: auto 20px; }
  header .social-icon {
    height: 20px; }
  header a {
    color: #000000;
    text-decoration: none; }

h2 {
  font-size: 50px; }

ul {
  font-size: 14px;
  display: flex;
  flex-direction: row;
  padding-left: 15px; }

li {
  list-style: none;
  margin: auto 15px; }

li:hover {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
  cursor: pointer; }

.chevron {
  height: 35px;
  display: block;
  margin-left: auto;
  margin-right: auto; }

footer {
  height: 400px;
  background: url(/portfolio/static/media/stephen-walker-1160866-unsplash.f2201e4f.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position-y: 125px; }

.copyright-section {
  color: #FFFFFF;
  text-align: center;
  position: relative;
  top: 360px; }

h1 {
  font-family: 'Ultra', serif;
  margin: 0px;
  font-size: 65px; }

.splash-image {
  height: 75vh;
  background: url(/portfolio/static/media/IMG_2284.bddaf9b1.JPG);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position-y: -25px;
  color: #FFFFFF; }

.splash-image-name {
  position: absolute;
  right: 50px;
  top: 350px; }

.splash-text {
  font-size: 1.5rem;
  width: 700px;
  margin: 30px auto;
  text-align: center; }

.about-section {
  margin-top: 75px; }

h2 {
  text-align: center; }

.about-details {
  display: flex;
  justify-content: space-between;
  align-content: center;
  padding-left: 100px;
  padding-right: 100px; }

.about-text {
  margin-top: 0px; }

.headshot {
  height: 150px;
  margin-right: 60px;
  border-radius: 150px; }

.about-columns {
  display: flex;
  justify-content: space-between;
  padding: 0px 100px; }
  .about-columns article {
    margin: 25px 45px; }

.about-tech-stack {
  width: 250px;
  font-weight: 200; }
  .about-tech-stack dl {
    margin-left: 10px; }

dd {
  margin-left: 10px; }

.about-education p {
  margin-left: 10px; }

.about-experience p {
  margin-left: 10px; }

.header {
  font-weight: 600;
  margin-bottom: 0px; }

.subhead {
  font-weight: 200;
  margin: 0px; }

.job-details {
  font-weight: 200; }

.projects-section {
  margin-top: 75px; }

.projects-display {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0px 150px; }

.project {
  width: 450px;
  height: 500px;
  margin: 0px 18px 35px 18px;
  border: .5px solid #DCDCDC;
  padding: 10px; }
  .project p {
    margin-top: 6px;
    margin-bottom: 0px; }

.project-image {
  width: 450px;
  -webkit-transition: -webkit-transform .5s ease;
  transition: -webkit-transform .5s ease;
  transition: transform .5s ease;
  transition: transform .5s ease, -webkit-transform .5s ease; }

.project-image:hover {
  -webkit-transform: scale(1.7);
          transform: scale(1.7);
  -webkit-transition: -webkit-transform 2s ease 1s;
  transition: -webkit-transform 2s ease 1s;
  transition: transform 2s ease 1s;
  transition: transform 2s ease 1s, -webkit-transform 2s ease 1s; }

.project-title {
  font-weight: 700;
  margin-bottom: 0px;
  font-size: 20px; }

.tech-stack {
  margin-top: 4px;
  height: 40px; }

.project-subhead {
  font-weight: 600; }

.project-details {
  margin-left: 15px; }

.project-specifics {
  position: relative;
  bottom: -20px; }
  .project-specifics p {
    margin-bottom: 10px; }

.contact-section {
  margin-top: 75px;
  text-align: center; }
  .contact-section hr {
    width: 300px; }

.social-icon {
  height: 30px;
  margin: 0px 10px;
  cursor: pointer; }

.social-section {
  margin-top: 25px;
  margin-bottom: 50px; }
  .social-section button {
    border: 0px; }

